// React Package Module
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

// MUI Package Module
import { styled, Box } from "@mui/material";

// Content Component
import NovelCard from "components/contents/NovelCard";

// Control Component
import Buttons from "components/controls/Button";

// Constant
import { CODE, LABEL, COLOR } from "common";

// API
import { getData } from "common/communication";

/** STYLE Ï†ïÏùò */
// Ìó§Îçî Ï†úÏô∏ ÏòÅÏó≠
const MainBox = styled(Box)({
	width: "80%",
	display: "flex",
	flexDirection: "column",
	margin: "0 auto",
});

// ÏÜåÏÑ§ Ïª¥Ìè¨ÎÑåÌä∏ Ïπ¥Îìú ÏòÅÏó≠
const NovelCardBox = styled(Box)({
	flexGrow: 1,
	width: "100%",
	margin: "0 auto",
	display: "flex",
	flexWrap: "wrap",
	boxSizing: "border-box",
});

// ÏÜåÏÑ§ Íµ¨Î∂Ñ Î≤ÑÌäº Î∞ïÏä§
const DivNovelBtn = styled(Box)({
	display: "flex",
	minHeight: 40,
});

/** Ï∞úÌïú ÏûëÌíà ÌÅ¥Î¶≠ Ïãú ÎÇòÏò§Îäî ÌéòÏù¥ÏßÄ */
const FavoriteNov = () => {
	/** STATE Ï†ïÏùò
	 * completeNovData: Î°úÍ∑∏Ïù∏Ìïú ÏûëÍ∞ÄÏùò ÏôÑÏÑ± ÏÜåÏÑ§ Îç∞Ïù¥ÌÑ∞
	 * inCompleteNovData: Î°úÍ∑∏Ïù∏Ìïú ÏûëÍ∞ÄÏùò ÎØ∏ÏôÑÏÑ± ÏÜåÏÑ§ Îç∞Ïù¥ÌÑ∞
	 * profile: Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ï†ÄÏû•Îêú ÌîÑÎ°úÌïÑ
	 */
	const [completeNovData, setCompleteNovData] = useState([]); 							// Î°úÍ∑∏Ïù∏Ìïú ÏûëÍ∞ÄÏùò ÏôÑÏÑ± ÏÜåÏÑ§ Îç∞Ïù¥ÌÑ∞
	const [incompleteNovData, setIncompleteNovData] = useState([]); 						// Î°úÍ∑∏Ïù∏Ìïú ÏûëÍ∞ÄÏùò ÎØ∏ÏôÑÏÑ± ÏÜåÏÑ§ Îç∞Ïù¥ÌÑ∞
	const [profile, setProfile] = useState(JSON.parse(localStorage.getItem("profile"))); 	// Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄÏóê Ï†ÄÏû•Îêú ÏÇ¨Ïö©Ïûê ÌîÑÎ°úÌïÑ
	const [selectedTab, setSelectedTab] = useState("complete"); 							// ÏÑ†ÌÉùÎêú Î©îÎâ¥
	const [isComplete, setIsComplete] = useState(true); 									// ÏÜåÏÑ§ ÏôÑÏÑ± Ïó¨Î∂Ä (Í∏∞Î≥∏Í∞í complete ÏÜåÏÑ§ ÌëúÏãú)

	// ÌéòÏù¥ÏßÄ Î†åÎçîÎßÅ Ïãú Ï∞úÌïú ÏôÑÏÑ± ÏÜåÏÑ§ Îç∞Ïù¥ÌÑ∞ Ï°∞Ìöå
	useEffect(() => {
		getPickNovels();
	}, []);

	// Ï∞úÌïú ÏôÑÏÑ± ÏÜåÏÑ§ Îç∞Ïù¥ÌÑ∞ Ï°∞Ìöå
	const getPickNovels = () => {
		getData("novel/getPickNovels", { login_id: profile.login_id })
			.then((data) => {
				setCompleteNovData(data);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	// Ï∞úÌïú ÏôÑÏÑ± ÏÜåÏÑ§ Îç∞Ïù¥ÌÑ∞ Ï°∞Ìöå
	const getPickIncompleteNovels = () => {
		getData("novel/getPickIncompleteNovels", { login_id: profile.login_id })
			.then((data) => {
				setIncompleteNovData(data);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	// ÏÉÅÏÑ∏Ï†ïÎ≥¥ ÌéòÏù¥ÏßÄÎ°ú ÌÅ¥Î¶≠Ìïú novel Ï†ïÎ≥¥ Î≥¥ÎÇ¥Í∏∞
	const navigate = useNavigate();

	const goToDetail = (novel) => {
		navigate("/novel_detail", { state: { props: novel } });
	};

	return (
		<>
			<MainBox>
				<DivNovelBtn>
					<Buttons
						type={CODE.BUTTON.BASIC}
						backgroundColor={COLOR.WHITE}
						color={COLOR.BLACK}
						fontWeight={selectedTab === "complete" && "bolder"}
						name={LABEL.BUTTONS.COMPLETE}
						isComplete={() => setIsComplete(true)}
						setSelectedTab={() => setSelectedTab("complete")}
						getPickNovels={getPickNovels}
						padding={0}
					/>
					<span
						style={{
							paddingTop: 8,
							marginLeft: 8,
							marginRight: 8,
							display: "inline-block",
						}}
					>
						|
					</span>
					<Buttons
						type={CODE.BUTTON.BASIC}
						backgroundColor={COLOR.WHITE}
						color={COLOR.BLACK}
						fontWeight={selectedTab === "incomplete" && "bolder"}
						name={LABEL.BUTTONS.IN_COMPLETE}
						isComplete={() => setIsComplete(false)}
						setSelectedTab={() => setSelectedTab("incomplete")}
						getPickIncompleteNovels={getPickIncompleteNovels}
						padding={0}
					/>
				</DivNovelBtn>
				<NovelCardBox>
					{/* Ï∞úÌïú ÎØ∏ÏôÑÏÑ± ÏÜåÏÑ§ Îç∞Ïù¥ÌÑ∞ */}
					{incompleteNovData &&
						isComplete === false &&
						incompleteNovData.map((list) => {
							return (
								<NovelCard
									key={list.main_seqno}
									title={list.title}
									description={list.description}
									created_date={list.created_date}
									created_user={list.created_user}
									cover_image={list.cover_image}
									pick_yn={list.pick_yn}
									user_id={profile.login_id}
									main_seqno={list.main_seqno}
									getPickIncompleteNovels={getPickIncompleteNovels}
									onClick={() => goToDetail(list)}
								/>
							);
						})}
					{/* Ï∞úÌïú ÏôÑÏÑ± ÏÜåÏÑ§ Îç∞Ïù¥ÌÑ∞ */}
					{completeNovData &&
						isComplete &&
						completeNovData.map((list) => {
							return (
								<NovelCard
									key={list.complete_seqno}
									title={list.complete_novel_title}
									genre_1={list.genre_1}
									genre_2={list.genre_2}
									keyword_1={list.keyword_1}
									keyword_2={list.keyword_2}
									keyword_3={list.keyword_3}
									genre_1_color={list.genre_1_color}
									genre_2_color={list.genre_2_color}
									keyword_1_color={list.keyword_1_color}
									keyword_2_color={list.keyword_2_color}
									keyword_3_color={list.keyword_3_color}
									description={list.description}
									like_count={list.like_count}
									created_date={list.created_date}
									cover_image={list.cover_image}
									pick_yn={list.pick_yn}
									user_id={profile.login_id}
									main_seqno={list.main_seqno}
									getPickNovels={getPickNovels}
									onClick={() => goToDetail(list)}
								/>
							);
						})}
				</NovelCardBox>
			</MainBox>
		</>
	);
};

export default FavoriteNov;
